'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Shield } from 'lucide-react'
import {
  ProgressIndicator,
  StepBasicInfo,
  StepIdentity,
  StepBusiness,
  StepTrustSignals,
} from '@/components/onboarding'
import {
  BasicInfoFormData,
  IdentityFormData,
  BusinessFormData,
  TrustSignalsFormData,
} from '@/lib/validations/onboarding'

const steps = [
  { id: 1, name: 'Basic Info', description: 'Your details' },
  { id: 2, name: 'Identity', description: 'Verification docs' },
  { id: 3, name: 'Business', description: 'Company info' },
  { id: 4, name: 'Trust Signals', description: 'Boost your score' },
]

interface OnboardingData {
  basicInfo: Partial<BasicInfoFormData>
  identity: Partial<IdentityFormData>
  business: Partial<BusinessFormData>
  trustSignals: Partial<TrustSignalsFormData>
}

export default function OnboardingPage() {
  const router = useRouter()
  const [currentStep, setCurrentStep] = useState(1)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [data, setData] = useState<OnboardingData>({
    basicInfo: {},
    identity: {},
    business: {},
    trustSignals: {},
  })

  const handleBasicInfoNext = (basicInfo: BasicInfoFormData) => {
    setData((prev) => ({ ...prev, basicInfo }))
    setCurrentStep(2)
  }

  const handleIdentityNext = (identity: IdentityFormData) => {
    setData((prev) => ({ ...prev, identity }))
    setCurrentStep(3)
  }

  const handleBusinessNext = (business: BusinessFormData) => {
    setData((prev) => ({ ...prev, business }))
    setCurrentStep(4)
  }

  const handleTrustSignalsSubmit = async (trustSignals: TrustSignalsFormData) => {
    setIsSubmitting(true)
    const finalData = { ...data, trustSignals }

    try {
      // Store data in session storage for the result page
      sessionStorage.setItem('onboardingData', JSON.stringify(finalData))

      // Calculate trust score via API
      const response = await fetch('/api/trust-score/calculate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(finalData),
      })

      const result = await response.json()
      sessionStorage.setItem('trustScoreResult', JSON.stringify(result))

      router.push('/onboarding/result')
    } catch (error) {
      console.error('Error calculating trust score:', error)
      // Still redirect to result page, it will show an error or fallback
      router.push('/onboarding/result')
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleBack = () => {
    setCurrentStep((prev) => Math.max(1, prev - 1))
  }

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="mx-auto max-w-2xl px-4">
        <div className="mb-8 text-center">
          <Link href="/" className="inline-flex items-center gap-2">
            <Shield className="h-8 w-8 text-blue-600" />
            <span className="text-xl font-bold">TrustLayer</span>
          </Link>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Check Your Eligibility</CardTitle>
            <CardDescription>
              Complete these steps to calculate your Trust Score and see your bank account options.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <ProgressIndicator steps={steps} currentStep={currentStep} />

            {currentStep === 1 && (
              <StepBasicInfo
                data={data.basicInfo}
                onNext={handleBasicInfoNext}
              />
            )}

            {currentStep === 2 && (
              <StepIdentity
                data={data.identity}
                onNext={handleIdentityNext}
                onBack={handleBack}
              />
            )}

            {currentStep === 3 && (
              <StepBusiness
                data={data.business}
                onNext={handleBusinessNext}
                onBack={handleBack}
              />
            )}

            {currentStep === 4 && (
              <StepTrustSignals
                data={data.trustSignals}
                onSubmit={handleTrustSignalsSubmit}
                onBack={handleBack}
                isSubmitting={isSubmitting}
              />
            )}
          </CardContent>
        </Card>

        <p className="mt-6 text-center text-sm text-gray-500">
          Already have an account?{' '}
          <Link href="/login" className="text-blue-600 hover:underline">
            Sign in
          </Link>
        </p>
      </div>
    </div>
  )
}
