'use client'

import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Checkbox } from '@/components/ui/checkbox'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { businessSchema, BusinessFormData, revenueOptions } from '@/lib/validations/onboarding'
import { ArrowLeft, ArrowRight } from 'lucide-react'

interface StepBusinessProps {
  data: Partial<BusinessFormData>
  onNext: (data: BusinessFormData) => void
  onBack: () => void
}

export function StepBusiness({ data, onNext, onBack }: StepBusinessProps) {
  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors },
  } = useForm<BusinessFormData>({
    resolver: zodResolver(businessSchema),
    defaultValues: {
      companyName: data.companyName ?? '',
      website: data.website ?? '',
      githubUrl: data.githubUrl ?? '',
      linkedinUrl: data.linkedinUrl ?? '',
      description: data.description ?? '',
      hasLocalBusiness: data.hasLocalBusiness ?? false,
      monthlyRevenue: data.monthlyRevenue ?? '0',
    },
  })

  const hasLocalBusiness = watch('hasLocalBusiness')
  const monthlyRevenue = watch('monthlyRevenue')

  return (
    <form onSubmit={handleSubmit(onNext)} className="space-y-6">
      <div className="space-y-2">
        <Label htmlFor="companyName">Company Name</Label>
        <Input
          id="companyName"
          placeholder="Your company or product name"
          {...register('companyName')}
        />
        {errors.companyName && (
          <p className="text-sm text-red-500">{errors.companyName.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="website">Company Website</Label>
        <Input
          id="website"
          type="url"
          placeholder="https://yourcompany.com"
          {...register('website')}
        />
        {errors.website && (
          <p className="text-sm text-red-500">{errors.website.message}</p>
        )}
        <p className="text-xs text-gray-500">
          Having a website with 6+ months domain age helps your trust score
        </p>
      </div>

      <div className="space-y-2">
        <Label htmlFor="githubUrl">GitHub Profile/Organization</Label>
        <Input
          id="githubUrl"
          type="url"
          placeholder="https://github.com/yourcompany"
          {...register('githubUrl')}
        />
        {errors.githubUrl && (
          <p className="text-sm text-red-500">{errors.githubUrl.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="linkedinUrl">LinkedIn Profile</Label>
        <Input
          id="linkedinUrl"
          type="url"
          placeholder="https://linkedin.com/in/yourprofile"
          {...register('linkedinUrl')}
        />
        {errors.linkedinUrl && (
          <p className="text-sm text-red-500">{errors.linkedinUrl.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="description">Business Description</Label>
        <Textarea
          id="description"
          placeholder="Briefly describe what your company does..."
          rows={3}
          {...register('description')}
        />
        {errors.description && (
          <p className="text-sm text-red-500">{errors.description.message}</p>
        )}
      </div>

      <div className="space-y-2">
        <Label>Monthly Revenue (USD)</Label>
        <Select
          value={monthlyRevenue}
          onValueChange={(value) => setValue('monthlyRevenue', value)}
        >
          <SelectTrigger>
            <SelectValue placeholder="Select your monthly revenue" />
          </SelectTrigger>
          <SelectContent>
            {revenueOptions.map((option) => (
              <SelectItem key={option.value} value={option.value}>
                {option.label}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <div className="rounded-lg border border-gray-200 p-4">
        <div className="flex items-start gap-4">
          <Checkbox
            id="hasLocalBusiness"
            checked={hasLocalBusiness}
            onCheckedChange={(checked) => setValue('hasLocalBusiness', !!checked)}
          />
          <div className="flex-1">
            <Label htmlFor="hasLocalBusiness" className="text-base font-medium">
              Registered Business in Home Country
            </Label>
            <p className="mt-1 text-sm text-gray-500">
              I have a legally registered business in my country of residence
            </p>
          </div>
        </div>
      </div>

      <div className="flex justify-between pt-4">
        <Button type="button" variant="outline" onClick={onBack} className="gap-2">
          <ArrowLeft className="h-4 w-4" />
          Back
        </Button>
        <Button type="submit" className="gap-2">
          Continue
          <ArrowRight className="h-4 w-4" />
        </Button>
      </div>
    </form>
  )
}
